<template>
    <div class="apiManagement-body">
        <div class="apiManagement-above">
            <el-form :inline="true" class="apiManagement-above-body">
                <el-form-item label="设备">
                    <el-select style="width:120px" size="small" v-model="formInline.deviceName" placeholder="东跨吸料天车">
                        <el-option label="东跨吸料天车" value="f73fe0d8688046e088bb073849aa0c3f" />
                        <el-option label="西跨吸料天车" value="b46a0faf11cc4000a4c290eba5cc949a" />
                    </el-select>
                </el-form-item>
                <el-form-item label="时间粒度">
                    <el-select style="width:120px" size="small" v-model="formInline.timeSize" placeholder="分钟">
                        <el-option label="分钟" value="Min" />
                        <el-option label="实时" value="Now" />
                    </el-select>
                </el-form-item>
                <el-form-item label="时间">
                    <el-date-picker style="width:220px" size="small" v-model="formInline.time" type="datetimerange"
                        range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间"
                        value-format="YYYY-MM-DD HH:mm:ss" />
                </el-form-item>
                <el-form-item>
                    <el-button @click="search" type="primary">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="apiManagement-under">
            <div class="apiManagement-under-body">
                <el-table :data="vocDate.vocDate1" height="450" style="width: 100%" max-height="50%"
                    class="apiManagement-under-body-table">
                    <el-table-column prop="name" label="设备名" width="500" />
                    <el-table-column prop="time" label="时间" width="500" />
                    <el-table-column prop="wind" label="风机运行信号">
                    </el-table-column>>
                </el-table>
                <div class=" apiManagement-under-body-pagination">
                    <el-pagination v-model:current-page="currPage1" v-model:page-size="pageSize1"
                        :page-sizes="[10, 20, 30, 40]" :small="small" :disabled="disabled" :background="background"
                        layout="total, sizes, prev, pager, next, jumper" :total="formInline.total" @size-change="search"
                        @current-change="search" />
                </div>
            </div>
        </div>
    </div>
</template>
  
<script setup>
import { getAirCarData } from "@/api/airCar";
import { ref, reactive, onMounted } from "vue";

const formInline = reactive({
    time: ['', ''],
    deviceName: 'f73fe0d8688046e088bb073849aa0c3f',
    timeSize: 'Min',
    total: ""
})


let currPage1 = ref(1)
let pageSize1 = ref(10)

function search() {
    let payload = {
        //要往后传的数据
        currPage: currPage1.value,
        pageSize: pageSize1.value,
        dataTime: formInline.timeSize,//时间粒度
        boxId: formInline.deviceName,//
        startTime: formInline.time[0],
        endTime: formInline.time[1],
    }
    console.log(formInline.timeSize);
    console.log(payload);
    getVocData(payload);
}

let vocDate = reactive({
    vocDate1: [],
})
let dataGet = null
function getVocData(payload) {
    getAirCarData(payload).then((res) => {

        vocDate.vocDate1 = []
        console.log(res);
        dataGet = res.data.data.dataSet
        formInline.total = res.data.data.totalCount
        //东
        if (formInline.deviceName == 'f73fe0d8688046e088bb073849aa0c3f') {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi['name'] = "东跨吸料天车"
                linshi['time'] = dataGet[x].createTime
                if (formInline.timeSize == 'Min') {

                    linshi['wind'] = dataGet[x].data[0].value
                }
                else {
                    linshi['wind'] = dataGet[x].value
                }
                vocDate.vocDate1.push(linshi)
            }
        }
        //西
        else {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi['name'] = "西跨吸料天车"
                linshi['time'] = dataGet[x].createTime
                if (formInline.timeSize == 'Min') {
                    linshi['wind'] = dataGet[x].data[0].value
                }
                else {
                    linshi['wind'] = dataGet[x].value
                }
                vocDate.vocDate1.push(linshi)
            }

        }

    });
}

//进入显示
onMounted(() => {
    search()
})     
</script>
  
<style scoped>
.apiManagement-body {
    width: 100%;
    height: 100%;
}

.apiManagement-above {
    position: absolute;
    width: 85%;
    height: 13%;
    background-color: white;
    margin-top: -2%;
}

.apiManagement-above-body {
    font-size: 5px;
    padding: 3%;
    margin-left: -2%;
    display: flex;
}



.apiManagement-above-body-select {
    margin-left: 0.5%;
    margin-top: -0.3%;
    width: 200px;
}

.apiManagement-above-body-button {
    margin-left: 1%;
    margin-top: -0.3%;
}

.apiManagement-under {
    position: absolute;
    background-color: white;
    width: 85%;
    height: 70%;
    margin-top: 5%;
}

.apiManagement-under-body {
    display: flex;
    padding: 1%;

}



.apiManagement-under-body-table {
    margin-left: 0%;

    width: 100%;
    height: 80%;
    overflow: hidden;
}

.apiManagement-under-body-pagination {
    display: inline;
    margin-top: 35%;
    margin-left: -65%;
}
</style>